  .logo {
    font-weight: bold;
    font-size: 20px;
  }
  
  .icons {
    display: flex;
    align-items: center;
  }
  
  .icon {
    margin-left: 15px;
    cursor: pointer;
    position: relative;
  }
  
  .iconImg {
    width: 20px;
    height: 20px;
  }
  
  .counter {
    width: 5px;
    height: 5px;
    background-color: red;
    border-radius: 50%;
    padding: 5px;
    font-size: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: -5px;
    right: -5px;
  }
  
  .notifications {
    position: absolute;
    top: 50px;
    right: 0;
    background-color: white;
    color: black;
    font-weight: 300;
    display: flex;
    flex-direction: column;
    padding: 10px;
  }
  
  .notification {
    padding: 5px;
    font-size: 14px;
  }
  
  .nButton {
    width: 80%;
    padding: 5px;
    margin-top: 10px;
  }